.App {
  /* text-align: center; */
}

.App-header {
  min-height: 5vh;
  width: 100%;
  position: fixed;
  padding: 1vh 1vw;
  font-family: 'century gothic';
  font-weight: bold;
  font-style: italic;
}

.App-body {
  /* min-height: 300vh; */
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.App-body h1 {
  font-size: calc(32px + 1vmin);
  font-family: "century gothic";
  font-weight: bold;
  font-style: italic;
  /* color: rgba(255, 0, 0, 1) */
}

.section-hero {
  width: 100%;
  min-height: 100vh;
  background-color: #ffffff;

  /* 背景画像の設定 */
  background-image: url("./img/background_hero_FHD_50.jpg");
	background-position: top center;
	background-repeat: no-repeat;
	background-attachment: fixed;
  background-size: cover;
  display: block;

  /* 子要素を上下中央にセンタリング */
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-about-me {
  width: 100%;
  min-height: 100vh;
  /* background-color: #98ffbf; */
  
  /* 背景画像の設定 */
  background-image: url("./img/background_profile_FHD_50.jpg");
	background-position: top center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
}

.section-header{
  width: 100%;
  height: 4em;
  padding-top: 1em;
  padding-bottom: 1em;
  display: block;
  /* background-color: rgba(255, 255, 255, 0.6); */
  text-align: center;
  /* font-size: 2vh; */
  font-size: 32px;
}

.section-about-me-body{
  width: 100%;
  height: 100%;
  display: flex;
  /* background-color: rgba(255, 0, 0, 0.6); */
  
  /* 子要素を上下中央にセンタリング */
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-me {
  min-width: 40vw;
  min-height: 50vh;

  display: block;
  
  background-color: rgba(255, 255, 255, 0.6);
  margin-right: auto;
  margin-left: auto;

  padding: 5em;
}
.about-me img {
  width: 300px;
}
.about-me p {
  color: #000000;
}

.section-sns {
  width: 100%;
  min-height: 100vh;

  /* 背景画像の設定 */
  background-image: url("./img/background_snspace_FHD_50.jpg");
	background-position: top center;
	background-repeat: no-repeat;
	background-attachment: fixed;
  background-size: cover;
  display: block;
}

.section-sns-body{
  width: 100%;
  height: 100%;
  
  /* 子要素を上下中央にセンタリング */
  display: flex;
  justify-content: center;
  align-items: center;

  display: flex;
  flex-wrap: wrap;
}

.Snspace {
  width: 400px;
  height: 500px;
  margin-top: 5vh;
  margin-bottom: 5vh;
  margin-left: 1vw;
  margin-right: 1vw;
  padding-top: 5vh;
  padding-bottom: 5vh;
  /* background-color: rgba(255, 255, 255, 0.6); */
  text-align: center;
}

.Snspace img {
  width: 300px;
}

.Snspace h2 {
  /* font-size: calc(18px + 1vmin);   */
  font-size: 24px;  
  font-family: "century gothic";
  font-weight: bold;
  /* color: rgba(0, 0, 255, 1) */
}

.Snspace p {
  /* font-size: calc(8px + 1vmin); */
  font-size: 18px;
}

.Snspace a {
  color: #000000;
  font-weight: bold;
  font-family: 'century gothic';
  /* font-size: calc(10px + 1vmin); */
  font-size: 24px;
}


.App-footer {
  background-color: #282c34;
  min-height: 30vh;
  width: 100%;
}